@import "../../../../app/styles/vars";
.container {
  z-index: 10;
  position: relative;
  display: flex;
  justify-content: center;
  align-items: center;
  border: 1px solid rgba(0, 0, 0, 0.2);
  width: 32px;
  height: 32px;
  box-shadow: var(--shadow-box);
  @media (max-width: $portrait-phones-max) {
    width: 26px;
    height: 26px;
  }
  .badge {
    display: flex;
    justify-content: center;
    align-items: center;
    background-color: white;
    width: 32px;
    height: 32px;
    .badge__icon {
      width: 16px;
      height: 16px;
      fill: var(--grey-color);
      &.rated {
        fill: var(--red-color);
      }
    }
  }
  .badge__bar {
    position: absolute;
    top: 50%;
    transform: translateY(-50%);
    right: calc(100% + 5px);
    display: flex;
    align-items: center;
    padding: 0 5px;
    background-color: white;
    border: 1px solid rgba(0, 0, 0, 0.2);
    box-shadow: 1px 1px 10px 1px rgba(0, 0, 0, 0.2);
    height: 32px;
    @media (max-width: $portrait-phones-max) {
      height: 26px;
    }
  }
}
